import React from 'react';
import '../../sass/main.scss';

const Faq = () => {
    return <div className="Faq" >
              <div className="Faq-question">
                   <h4>- Kako naruiti?</h4>
                   <p>Narudbe vrite tako to dodate artikal u korpu. Kada zavrite sa dodavanjem artikala u korpu, otvorite korpu (gordnji desni ugao) i kliknete gumb <b>"Nastavi"</b>. Zatim unesete svoje podatke koje forma zahtijeva i pritisnete gumb <b>"POALJI"</b> i uspjeno ste zavrili kupovinu. </p>
              </div>
              <div className="Faq-question">
                    <h4>- Kako funkcioniu dostava i plaanje?</h4>
                    <p> Plaate pouzeem. Dostavu vrimo u saradnji sa firmom koja je zaduena za brzu dostavu: <b> "X-Express" </b>. Rok isporuke: 24-48 sati. Ukoliko dostava kasni, mi ne odgovaramo jer mi ne moemo nita promijeniti i utjecati na situaciju. Primaoc ima pravo otvoriti paket, ukoliko bude nepravilnosti(oteenje i sl.) primaoc moe izvriti povrat i novac e biti vraen.</p>
              </div>
              <div className="Faq-question">
                    <h4>- Da li su testeri originalni i kakva je ambalaa? </h4>
                    <p> Mi prodajemo iskljuivo originalne testere od pravih proizvoaa. Ambalae su takoer originalne. </p>
              </div>
              <div className="Faq-question">
                    <h4> Imam popust ali kada otvorim korpu nije izraunat popust u ukupnu cijenu? </h4>
                    <p> Popust se izraunava jedino na <b>"Checkout-u".</b> </p>
              </div>
    </div>
} 

export default Faq;