import React from 'react';
import '../../sass/main.scss';

const About = () => {
    return <about>
              <div className="About">
                   <div className="About-text">
                       <p>U Vestige vjerujemo da bi ljepota trebala biti dostupna svima jer ona pridonosi samopouzdanju i mijenja na bolje. Kada se ljudi osjeaju dobro, lake postiu svoje ciljeve, a zauzvrat ine ljepim svijet oko sebe. Uvijek se trudimo u ponudi imati najprikladnije proizvode za vas. </p>
                       <p>Na cilj je, u isto vrijeme, dostaviti proizvode to sigurnije i to bre. Sve proizvode imamo na zalihama, a zahvaljujui naoj tehnologiji, odlinim vjetinama upravljanja naeg logistikog i distribucijskog centra, vaa narudba e biti isporuena u najkraem moguem roku. Nae parfeme moete naruiti putem drutvenih mrea Facebook i Instagram. </p>
                        <p>Vestige smatra svakog svog klijenta jedinstvenim, zato se trudimo da svakom naem klijentu pruimo ljubav koju nosimo do naih proizvoda, usluge i stunosti, kako bi to postala zajednika strast.  Zadovoljstvo kupaca je za nas uvijek bilo i uvijek e biti na prvom mjestu.</p>
                   </div>
                   <div className="About-img">
                       <div className="About-img-logo">
                            <img  src={require(`../../assets/images/logo.png`)}/>
                       </div>
                      <img  src={require(`../../assets/images/about.jpg`)}/>
                   </div>
              </div>
          </about >
}

export default About